.App {
  text-align: center;
  margin-left: 30px;
}
.App-intro {
  font-size: large;
}
.comp {
  width: 400px;
  word-break: break-word;
}
.snippetWrapper {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}
h3 {
  margin: 30px 0;
  font-size: 30px;
  font-weight: bold;
  text-align: left;
}
.withJson {
  margin-left: 20px;
  text-align: left;
}
.adsum-clock-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.adsum-clock-wrapper .adsum-clock {
  color: lightgray;
  text-align: center;
}
.adsum-clock-wrapper .adsum-clock .day-date {
  margin: 0;
  letter-spacing: -2px;
  color: #ababab;
}
.adsum-clock-wrapper .adsum-clock .time {
  font-weight: bold;
  color: #404040;
}
.adsum-clock-wrapper .adsum-clock .divider {
  width: 40px;
  margin: 0 auto;
  border-width: 3px;
}
@media (orientation: landscape) {
  .adsum-clock-wrapper {
    height: 100%;
  }
  .adsum-clock-wrapper .adsum-clock {
    font-size: 2.3em;
  }
  .adsum-clock-wrapper .adsum-clock .day-date {
    font-size: 1.0em;
  }
}
@media (orientation: portrait) {
  .adsum-clock-wrapper .adsum-clock {
    font-size: 1.8em;
    text-align: right;
  }
  .adsum-clock-wrapper .adsum-clock .day-date {
    font-size: 0.7em;
  }
}



